import { useEffect, useRef } from 'react';

const useScrollRestoration = (key, dependencies = []) => {
    const scrollPositionRef = useRef(0);
    const storageKey = `scroll_${key}`;

    // Save scroll position to localStorage
    const saveScrollPosition = () => {
        try {
            const scrollPosition = window.pageYOffset || document.documentElement.scrollTop;
            localStorage.setItem(storageKey, scrollPosition.toString());
            scrollPositionRef.current = scrollPosition;
        } catch (error) {
            console.warn('Failed to save scroll position:', error);
        }
    };

    // Restore scroll position from localStorage
    const restoreScrollPosition = () => {
        try {
            const savedPosition = localStorage.getItem(storageKey);
            if (savedPosition && savedPosition !== '0') {
                const position = parseInt(savedPosition, 10);
                if (!isNaN(position)) {
                    // Use requestAnimationFrame to ensure DOM is ready
                    requestAnimationFrame(() => {
                        window.scrollTo({
                            top: position,
                            behavior: 'instant'
                        });
                        console.log(` Scroll position restored: ${position}px`);
                    });
                }
            }
        } catch (error) {
            console.warn('Failed to restore scroll position:', error);
        }
    };

    // Save scroll position when navigating away
    useEffect(() => {
        const handleBeforeUnload = () => {
            saveScrollPosition();
        };

        const handleScroll = () => {
            scrollPositionRef.current = window.pageYOffset || document.documentElement.scrollTop;
        };

        // Save scroll position periodically and on important events
        window.addEventListener('scroll', handleScroll, { passive: true });
        window.addEventListener('beforeunload', handleBeforeUnload);

        // Save scroll position every 2 seconds during scrolling
        let scrollTimer;
        const handleScrollEnd = () => {
            clearTimeout(scrollTimer);
            scrollTimer = setTimeout(() => {
                saveScrollPosition();
            }, 100);
        };

        window.addEventListener('scroll', handleScrollEnd, { passive: true });

        return () => {
            window.removeEventListener('scroll', handleScroll);
            window.removeEventListener('scroll', handleScrollEnd);
            window.removeEventListener('beforeunload', handleBeforeUnload);
            saveScrollPosition(); // Save on unmount
            clearTimeout(scrollTimer);
        };
    }, [storageKey]);

    // Restore scroll position when dependencies change (e.g., data loads)
    useEffect(() => {
        const timer = setTimeout(() => {
            restoreScrollPosition();
        }, 100); // Small delay to ensure content is rendered

        return () => clearTimeout(timer);
    }, dependencies);

    return {
        saveScrollPosition,
        restoreScrollPosition,
        currentScrollPosition: scrollPositionRef.current
    };
};

export default useScrollRestoration;
