import { useState, useEffect, useCallback, useRef } from 'react';
import useAxiosPrivate from './useAxiosPrivate';
import useLocalStorage from './useLocalStorage';
import { useCacheInvalidation } from '../context/CacheInvalidationProvider';

const useDataCache = (cacheKey, fetchFunction, dependencies = []) => {
    const axiosPrivate = useAxiosPrivate();
    const [cachedData, setCachedData] = useLocalStorage(cacheKey || 'null_key', null);
    const [lastFetchTime, setLastFetchTime] = useLocalStorage(`${cacheKey || 'null_key'}_timestamp`, null);
    const [data, setData] = useState(cacheKey ? cachedData : null);
    const [isLoading, setIsLoading] = useState(cacheKey && !cachedData);
    const [error, setError] = useState(null);
    const autoRefreshRef = useRef(null);
    
    const { registerCacheInvalidation } = useCacheInvalidation();

    // Cache duration in milliseconds (5 minutes)
    const CACHE_DURATION = 5 * 60 * 1000;
    // Auto-refresh interval in milliseconds (30 seconds)
    const AUTO_REFRESH_INTERVAL = 30 * 1000;

    const isCacheValid = useCallback(() => {
        if (!lastFetchTime) return false;
        return Date.now() - lastFetchTime < CACHE_DURATION;
    }, [lastFetchTime]);

    const fetchData = useCallback(async (forceRefresh = false) => {
        // Skip fetching if no cache key is provided
        if (!cacheKey) {
            setData(null);
            setIsLoading(false);
            return null;
        }

        if (!forceRefresh && isCacheValid() && cachedData) {
            setData(cachedData);
            setIsLoading(false);
            return cachedData;
        }

        setIsLoading(true);
        setError(null);

        try {
            const result = await fetchFunction(axiosPrivate);
            
            // Validate result before caching
            if (result !== null && result !== undefined) {
                setCachedData(result);
                setLastFetchTime(Date.now());
                setData(result);
                console.log(` Cache updated for ${cacheKey}:`, result?.length || 'data loaded');
            } else {
                console.warn(` Invalid data received for ${cacheKey}`);
                if (cachedData) {
                    console.log(`Using existing cache for ${cacheKey}`);
                    setData(cachedData);
                }
            }
            
            return result;
        } catch (err) {
            console.error(` Error fetching data for ${cacheKey}:`, err);
            setError(err);
            
            // If cache exists and error occurs, use cached data
            if (cachedData) {
                setData(cachedData);
                console.log(` Using cached data for ${cacheKey} due to fetch error`);
            }
            
            throw err;
        } finally {
            setIsLoading(false);
        }
    }, [axiosPrivate, fetchFunction, cacheKey, isCacheValid, cachedData, setCachedData, setLastFetchTime]);

    const startAutoRefresh = useCallback(() => {
        if (autoRefreshRef.current) {
            clearInterval(autoRefreshRef.current);
        }

        autoRefreshRef.current = setInterval(async () => {
            try {
                console.log(` Auto-refreshing ${cacheKey}...`);
                const newData = await fetchData(true);
                if (newData) {
                    console.log(` Auto-refresh completed for ${cacheKey}`);
                }
            } catch (error) {
                console.error(` Auto-refresh failed for ${cacheKey}:`, error);
                // Don't throw error during auto-refresh to avoid breaking the UI
            }
        }, AUTO_REFRESH_INTERVAL);
    }, [fetchData, cacheKey]);

    const stopAutoRefresh = useCallback(() => {
        if (autoRefreshRef.current) {
            clearInterval(autoRefreshRef.current);
            autoRefreshRef.current = null;
        }
    }, []);

    const invalidateCache = useCallback(() => {
        setCachedData(null);
        setLastFetchTime(null);
        setData(null);
    }, [setCachedData, setLastFetchTime]);

    // Initial fetch
    useEffect(() => {
        if (cacheKey) {
            fetchData();
        }
    }, [...dependencies, fetchData]);

    // Start auto-refresh when component mounts and data is loaded
    useEffect(() => {
        if (cacheKey && data && !isLoading) {
            startAutoRefresh();
        }

        return () => {
            stopAutoRefresh();
        };
    }, [cacheKey, data, isLoading, startAutoRefresh, stopAutoRefresh]);

    // Cleanup on unmount
    useEffect(() => {
        return () => {
            stopAutoRefresh();
        };
    }, [stopAutoRefresh]);

    // Register for global cache invalidation
    useEffect(() => {
        if (cacheKey) {
            const unregister = registerCacheInvalidation(cacheKey, invalidateCache);
            return unregister;
        }
    }, [cacheKey, registerCacheInvalidation, invalidateCache]);

    return {
        data,
        isLoading,
        error,
        refetch: () => fetchData(true),
        invalidateCache,
        isCacheValid: isCacheValid(),
        lastFetchTime
    };
};

export default useDataCache;
