import {useCallback, useEffect, useState} from 'react';
import {useParams} from 'react-router-dom';
import useAxiosPrivate from '../hooks/useAxiosPrivate';
import useDataCache from '../hooks/useDataCache';
import useScrollRestoration from '../hooks/useScrollRestoration';
import '../styles/districtPage.css';
import ImportDistrict from '../components/ImportDistrict';
import DistrictButtons from '../components/DistrictButtons';
import BuildingsList from '../components/BuildingsList';
import {BounceLoader} from 'react-spinners';
import {DragDropContext, Droppable} from 'react-beautiful-dnd';

const DistrictSelectionPage = () => {
    const axiosPrivate = useAxiosPrivate();
    const {areaId} = useParams();
    const [districts, setDistricts] = useState([]);
    const [currentDistrict, setCurrentDistrict] = useState({});
    const [newDistrictUploaded, setNewDistrictUploaded] = useState(0);

    // Caching setup for districts
    const fetchDistricts = useCallback((axiosPrivate) => 
        axiosPrivate.get(`/api/district/area/${areaId}`)
            .then(response => response.data)
    , [areaId]);

    const {
        data: districtsData,
        isLoading: isLoadingDistricts,
        refetch: refetchDistricts,
        invalidateCache: invalidateDistrictsCache
    } = useDataCache(`districts_${areaId}`, fetchDistricts, [areaId]);

    // Caching setup for buildings
    const fetchBuildings = useCallback((axiosPrivate) => 
        currentDistrict._id ? 
            axiosPrivate.get(`/api/district/${currentDistrict._id}`)
                .then(response => response.data.buildings) :
            Promise.resolve([])
    , [currentDistrict._id]);

    const {
        data: buildings,
        isLoading: isLoadingBuildings,
        refetch: refetchBuildings,
        invalidateCache: invalidateBuildingsCache
    } = useDataCache(
        currentDistrict._id ? `buildings_${currentDistrict._id}` : null, 
        fetchBuildings, 
        [currentDistrict._id]
    );

    // Scroll restoration
    const { saveScrollPosition } = useScrollRestoration(
        `district_${areaId}`, 
        [buildings?.length, districts?.length]
    );

    // Update local districts state when cached data changes
    useEffect(() => {
        if (districtsData) {
            setDistricts(districtsData);
            // Set current district if not already set and we have districts
            if (!currentDistrict?._id && districtsData.length > 0) {
                setCurrentDistrict(districtsData[0]);
            }
        }
    }, [districtsData]);

    // Manual buildings fetching function (for district buttons)
    const getBuildings = useCallback(async (id) => {
        // Save scroll position before changing districts
        saveScrollPosition();
        
        // Find and set the new current district
        const newDistrict = districts.find(district => district._id === id);
        if (newDistrict) {
            setCurrentDistrict(newDistrict);
        }
    }, [districts, saveScrollPosition]);

    // Handle district change and cache invalidation
    const handleDistrictChange = useCallback(async () => {
        // Invalidate caches when new district is uploaded
        invalidateDistrictsCache();
        // Always invalidate buildings cache regardless of current district
        invalidateBuildingsCache();
        
        // Refetch districts
        await refetchDistricts();
    }, [invalidateDistrictsCache, invalidateBuildingsCache, refetchDistricts]);

    // Handle district reordering
    const onDragEnd = async (result) => {
        if (!result.destination) return;
        
        const items = Array.from(districts);
        const [reorderedItem] = items.splice(result.source.index, 1);
        items.splice(result.destination.index, 0, reorderedItem);
        
        // Optimistically update the UI
        setDistricts(items);
        
        try {
            await axiosPrivate.post('/api/district/reorder', {
                districts: items.map((district, index) => ({
                    id: district._id,
                    priority: index + 1,
                })),
            });
            
            // Invalidate cache after successful reorder
            invalidateDistrictsCache();
        } catch (error) {
            console.error('Failed to reorder districts', error);
            // Revert the UI change on error
            setDistricts(districts);
        }
    };

    const isLoading = isLoadingDistricts || isLoadingBuildings;

    return (
        <div className="districtPageContainer" style={{padding: '20px'}}>
            {isLoading && (
                <div style={{position: 'absolute', top: '50%', left: '50%', border: 0}}>
                    <BounceLoader color="#3498db"/>
                </div>
            )}
            <h1 style={{marginBottom: '20px'}}>Districts Page</h1>
            
            {/* Cache status indicator */}
            <div className="cacheStatus">
                <span><span className="autoRefreshIndicator"></span> Auto-refresh: Active</span>
                {buildings && (
                    <span> Buildings: {buildings.length} cached</span>
                )}
                {isLoadingBuildings && (
                    <span className="loadingIndicator"> Refreshing data...</span>
                )}
            </div>
            
            <ImportDistrict
                areaId={areaId}
                setNewDistrictUploaded={setNewDistrictUploaded}
                onDistrictCreated={handleDistrictChange}
                style={{marginBottom: '20px'}}
            />
            <div style={{marginTop: '20px'}}>
                <h2 style={{marginBottom: '15px'}}>Current District Name: {currentDistrict.name}</h2>
                <DragDropContext onDragEnd={onDragEnd}>
                    <Droppable droppableId="districts">
                        {(provided) => (
                            <div {...provided.droppableProps} ref={provided.innerRef}>
                                <DistrictButtons
                                    districts={districts}
                                    getBuildings={getBuildings}
                                    setCurrentDistrict={setCurrentDistrict}
                                />
                                {provided.placeholder}
                            </div>
                        )}
                    </Droppable>
                </DragDropContext>
                <BuildingsList buildings={buildings || []} isLoading={isLoadingBuildings}/>
            </div>
        </div>
    );
};

export default DistrictSelectionPage;